import { AbstractControl } from "@angular/forms";

export function dataFabricacaoValidator(control: AbstractControl) {
    
    if(control.get('DataValidade')?.value && control.get('DataFabricacao')?.value ){
        
        let DataValidade = new Date (control.get('DataValidade')?.value as Date);
        DataValidade.setDate(DataValidade.getDate()+1);

        let DataFabricacao = new Date (control.get('DataFabricacao')?.value as Date);
        DataFabricacao.setDate(DataFabricacao.getDate()+1);

        if(DataFabricacao > DataValidade){


            return { datafabricacao: true };
        }

    }
    return null;

    
}